@import url(https://fonts.googleapis.com/css?family=Poppins:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i);
.navbar-wrapper{padding:2rem 10%}.navbar-wrapper .navbar-links-wrapper{padding:0;display:flex;justify-content:center;flex-wrap:wrap;list-style:none;box-shadow:0 0 100px 0 #00000054}.navbar-wrapper .navbar-links-wrapper .navbar-item{position:relative;transition:.4s;flex:1 0 20%;height:75px;display:flex;align-items:center;justify-content:center;cursor:default}.navbar-wrapper .navbar-links-wrapper .navbar-item a{transition:.3s;color:#3b3939}.navbar-wrapper .navbar-links-wrapper .navbar-item:hover a{transition:.3s;color:#000}.navbar-wrapper .navbar-links-wrapper .navbar-item::before{content:"";position:absolute;color:#000;transition:.4s;width:0;left:25%;bottom:1.24rem}.navbar-wrapper .navbar-links-wrapper .navbar-item:hover{transition:.4s;flex:2 0 25%;-webkit-backdrop-filter:brightness(1.1);backdrop-filter:brightness(1.1)}.navbar-wrapper .navbar-links-wrapper .navbar-item:hover::before,.navbar-wrapper .navbar-links-wrapper .navbar-item.active::before{border:1px solid #000;animation-name:line_animation;animation-duration:.4s;animation-timing-function:linear;animation-fill-mode:forwards}@keyframes line_animation{from{left:50%;width:0}to{left:25%;width:50%}}.navbar-wrapper .navbar-links-wrapper .navbar-item.active{transition:.4s;flex:3 0 25%;-webkit-backdrop-filter:brightness(1.5);backdrop-filter:brightness(1.5)}
.product-item{padding:1rem;background:#fff;box-shadow:0 0 100px 0 #00000054;border-radius:5%}.product-item-delete-mark{cursor:pointer;font-size:1.25em;position:absolute;top:10px;right:15px;color:red}
.product-item-wrapper{padding:1.25rem}.inline-load-mocked{cursor:pointer;color:#9e0000}
.btn-delete-group button{width:100%}
.sort-by-count,.sort-by-name{transition:.4s;display:inline-block}.rotate{transition:.4s;transform:rotateX(180deg)}
.btn-product-modal button{width:100%}
.product-item{background:#fff;box-shadow:0 0 100px 0 #00000054;border-radius:10%}.product-item .product-item-redact{cursor:pointer;position:absolute;top:10px;right:40px;color:#f39d03}
.new-comment-wrapper{border-radius:5px;box-shadow:0 0 100px 0 #00000054;background:rgba(255,255,255,.4);margin-bottom:50px}.new-comment-wrapper .error-text{color:red}
.comment-wrapper{border-radius:5px;background:rgba(255,255,255,.1);box-shadow:0 0 100px 0 #00000054}.comment-wrapper .comment-item{border-bottom:1px solid #000;margin-bottom:1rem}.comment-wrapper .comment-item:last-child{border-bottom:none}.comment-wrapper .comment-item .comment-item-date{margin-bottom:.25rem;color:#000;font-size:12px}.comment-wrapper .comment-item .comment-item-text{color:#000;font-size:18px}
*{margin:0;padding:0;box-sizing:border-box;font-family:inherit;outline:none !important;list-style:none !important;text-decoration:none !important}*:after,*:before{box-sizing:border-box;padding:0;margin:0}html{min-height:100%;background:linear-gradient(146deg, rgba(47, 235, 249, 0.66) 0%, rgba(54, 234, 249, 0.664) 8.1%, rgba(70, 230, 249, 0.677) 15.5%, rgba(89, 224, 250, 0.695) 22.5%, rgba(109, 217, 250, 0.72) 29%, rgba(128, 208, 251, 0.748) 35.3%, rgba(147, 197, 251, 0.78) 41.2%, rgba(164, 185, 252, 0.813) 47.1%, rgba(180, 172, 252, 0.847) 52.9%, rgba(194, 158, 253, 0.88) 58.8%, rgba(207, 143, 253, 0.912) 64.7%, rgba(218, 129, 254, 0.94) 71%, rgba(227, 115, 254, 0.965) 77.5%, rgba(233, 104, 255, 0.983) 84.5%, rgba(238, 95, 255, 0.996) 91.9%, #ef5cff 100%) no-repeat}html body{background-color:inherit;font-family:"Poppins",ysans-std,sans-serif}.c-pointer{cursor:pointer}
